import React from 'react';

const ContactSection = (props) => {
  return (
    <section id="contact" className="section contact">
      <div className="wrapper">
        <h2 className="section__title contact">
          Contact
        </h2>
        <hr />
        <p>
          Une question ? Envie de travailler ensemble ?
        </p>
        <p>
          Vous pouvez m'envoyer un email  l'adresse <a href="http://www.google.com/recaptcha/mailhide/d?k=01k10yCxni6j0afa994fA5FQ==&amp;c=x-sLvXggfI6fyFoUlfXYDlvBRI_yFa1RfYtaIRdAyT0=" onClick={e => {e.preventDefault(); window.open('http://www.google.com/recaptcha/mailhide/d?k\x3d01k10yCxni6j0afa994fA5FQ\x3d\x3d\x26c\x3dx-sLvXggfI6fyFoUlfXYDlvBRI_yFa1RfYtaIRdAyT0\x3d', '', 'toolbar=0,scrollbars=0,location=0,statusbar=0,menubar=0,resizable=0,width=500,height=300'); return false;}} title="Reveal this e-mail address">t...@vandenhede.me</a> ou bien remplir le formulaire ci-dessous.
        </p>
        <hr />
        <form class="contact-form" action="//formspree.io/thomas@vandenhede.me" method="POST">
          <h3>Vous souhaitez m'crire ? J'ai tout prvu ;).</h3>
          <p>
            Vous vous appelez <input className="contact-form__input" type="text" name="name" placeholder="Jean Dupont" required /> et vous souhaiteriez m'crire de l'adresse <input className="contact-form__input" type="email" name="_replyto" placeholder="jeandupont@exemple.fr" required /> pour me dire :<br />
            <textarea className="contact-form__textarea" name="message" rows="2" placeholder="Message" required></textarea>
            <input className="hidden" type="text" name="_gotcha" style={{'display':'none'}} />
            <input className="hidden" type="hidden" name="_subject" value="Message via http://domain.com" />
          </p>
          <p>
            <input className="btn btn--rounded btn--shadow submit" type="submit" value="Envoyer" />
          </p>
        </form>
      </div>
    </section>
  );
};

export default ContactSection;


