import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

export default class Nav extends Component {
  render() {
    return (
      <nav className="nav">
        <ul className="nav__list">
          <NavLink
            exact
            to="/"
            className="nav__link"
            activeClassName="nav__link nav__link--active"> propros</NavLink>
          <NavLink
            exact
            to="/skills"
            className="nav__link"
            activeClassName="nav__link nav__link--active">Comptences</NavLink>
          <NavLink
            exact
            to="/portfolio"
            className="nav__link"
            activeClassName="nav__link nav__link--active">Ralisations</NavLink>
          <NavLink
            exact
            to="/contact"
            className="nav__link"
            activeClassName="nav__link nav__link--active">Contact</NavLink>
        </ul>
      </nav>
    );
  }
}


