import React from 'react';

const SkillsSection = (props) => {
  return (
    <section id="skills" className="section skills">
      <div className="wrapper">
        <h2 className="section__title">
          Comptences
        </h2>
        <hr />
        <h3>Mes langages et techniques</h3>
        <ul>
          <li>SCSS (Sass)</li>
          <li>Bootstrap</li>
          <li>Flexbox</li>
          <li>CSS Grid Layout</li>
          <li>Responsive Design</li>
          <li>React.js</li>
          <li>jQuery</li>
          <li>CSS Animations</li>
          <li>CSS Transitions</li>
        </ul>
        <h3>Mes outils</h3>
        <ul>
          <li>Sublime Text</li>
          <li>Google Chrome</li>
          <li>Terminal Linux</li>
          <li>Git</li>
        </ul>
        <p>
          J'cris mon code CSS dans le langage Sass (syntaxe SCSS) en respectant la convention <a href="http://getbem.com/" target="_blank" rel="noopener noreferrer">BEM</a> (Block Element Modifier) pour nommer mes classes.
        </p>
        <p>
          Je me base principalement sur les bonnes pratiques de <a href="https://sass-guidelin.es/" target="_blank" rel="noopener noreferrer">sass-guilelin.es</a> pour crire un code Sass propre et consistent que j'organise selon l'<a href="https://github.com/HugoGiraudel/sass-boilerplate" target="_blank" rel="noopener noreferrer">architecture 7-1</a>.
        </p>
      </div>
    </section>
  );
};

export default SkillsSection;


